/* Animate Nav â€“ Alex Cican */
nav {
  color: $bodycolor;
  position: fixed;
  top: 0px;
  right: 0px;
  z-index: 1000;
  cursor: pointer;
  transform: translate(0,0);
  transition: transform .4s, height .3s, background .4s;
  animation: firstFadeInAndDrop .5s;
}
nav a {
  text-decoration: none;
  border-bottom: 0px;
}
/* Animate Drop Down of Menu */
@keyframes firstFadeInAndDrop {
  0%   {transform: translate(0,-54px);}
  100% {transform: translate(0,0);}
}
/* increases menu width & height NAV BACK COLOUR */
nav.expanded {
  width: 100%;
  height: 100%;
  cursor: default;
  background: $lightgrey;
  background: $bodycolor;
  opacity: .98;
  z-index:997;
  overflow: scroll;
}
nav.expanded {
  body {
    filter:blur(5px)
  }
}
/* positions navigation content */
#navigation {
    transform: translate(0, -1000px);
    opacity: 0;
    display: none;
    transition: transform 5s, opacity 7s;
    color: white;
    margin: 0;
    padding-top: $xl-space;
    .span12 {
      margin-bottom: $l-space; /* nav spacing*/
    }
    a {
    }
    a:hover {
      text-decoration: none;
      border-bottom: .1em solid white;
    }
    h1, h2 {
      text-align: center;
      color: $accent-color;
      color: white;
      width:  auto;
      display:inline-block;
      line-height: 1;
      margin: $m-space 0;
    }
    h1 a {
      color: #fff;
    }

}
/* shows navigation */
nav.expanded #navigation {
    display: block;
    margin: 0;
    opacity: 1;
    position: relative;
    transform: translate(0, 0);
    width: 100%;
    z-index: 998;
}
/* SUB NAVIGATION ON FULL SCREEN NAV  */
.submenu {
    text-align: left;
    width: 100%;
    text-transform: uppercase;
}
.submenu a {
    text-decoration: none;
    border-bottom: none;
    color: white;
}
.submenu ul {
    list-style: none;
}
.submenu li {
    display: inline-block;
    margin-right: $m-space;
}
.submenu li:last-of-type {
    margin-right: 0;
}
/* Bar Transition to X  */
#nav-container {
    position: fixed;
    top: $m-space;
    right: $l-space;
    margin: 0;
    z-index: 999;
}
.toggle-icon {
    position: relative;
    display: block;
    width: 100%;
    margin: 0;
    cursor: pointer;
    transition: margin 600ms;
}
/* Positiioning Bars  */
.toggle-icon .bar {
    display: block;
    width: 3em;
    height: .3em;
    margin-bottom: .6em;
    transition-duration: 600ms;
    transition-property: transform, margin, border-right-color, box-shadow;
    background: red;
}
.toggle-icon .bar:nth-of-type(3) {
    margin-bottom: 0;
}
.toggle-icon .bar:nth-of-type(2) {
    transition: opacity 300ms;
}
.pushed .bar {
    background: red;
}
.pushed .bar:nth-of-type(2) {
    opacity: 0;
}
.pushed .bar:nth-of-type(1) {
    transform: translateY(.9em) rotate(-135deg);
}
.pushed .bar:nth-of-type(3) {
    transform: translateY(-.9em) rotate(-45deg);
}
/* Shrink Title on scroll */
.shrink {width: 200px;}
.no_scroll { overflow: hidden; }
/* End source navigation */

/* On page navigation  */
.page-menu {
  padding-bottom: $m-space;
  padding-top: $m-space;
}
.page-menu a {
