  .question {
    cursor: pointer !important;
    margin: 0;
    position: relative;
  }
  .answer {
    display: none;
    margin: 1em 0 0 0;
    padding-left: 3em;
  }
  .answer p {
    margin:0!important;
  }
  .question span {
    margin: 0;
  }
  .question.hello {
    padding-left: 3em;
  }
  .hello:before {
    position: absolute;
    content: 'add';
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 700;
    transform: translateY(-50%);
    left: 0;
    color: red;
    animation: roll 3s infinite;
    transform: rotate(-135deg);
  }
  .question-block {
    margin-bottom: .5em;
    font-size: 1em;
    background: #f1f1f1;
    padding: 1em;
  }
