  }




/* Person Layout Grey Block */

  .greyblock {
    padding: .75em;
    background: #f1f1f1;
    display: flex;
    align-items: center;
    border: 1px solid #f1f1f1;
  }
  .centered {
    display: flex;
    align-items: center;
  }
  .greyblock-details {
    overflow: hidden;
    padding-left: 1em;
    width: 50%;
  }
  .greyblock-details span {
    overflow: hidden;
    text-transform: uppercase;
  }

  .greyblock:hover {
    border: 1px solid #222;
  }





  /* Person Layout Grey Block */

    .indexblock {
      padding: 0;
      background: #f1f1f1;
      display: flex;
      align-items: center;
      border: $theborder;
      position: relative;
      h2 {
        width: 100%;
        background: $lightgrey;
        padding: $s-space;
        top:0;
        left:0;
        margin: 0;
        border-bottom: $theborder;
        text-transform: uppercase;
      }
      span {
        position: absolute;
        padding: $s-space;
        bottom:0;
        left:0;
        width: 100%;
        background: $lightgrey;
        display: block;
      }
    }
    .centered {
      display: flex;
      align-items: center;
    }

    .indexblock:hover {
    }


    /* Text Squares Beside Square Image */

    .fixed-square-wrapper {
      width:100%;
      position:relative;
      margin-bottom:0;
      height: 25em;
      margin: 0;
    }
    .fixed-text {
      position:absolute;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      left: 50%;
      width: 80%;
      font-weight: bold;
      font-size: 1.4em;
      color: white;
      margin:0 auto;
      a {
        color: white;
        text-decoration: none;
        border-bottom: .1em solid white

      }
      a:hover {
        border-bottom: .1em solid red;
        color: red;

      }
    }



    @media only screen and (max-width: 480px) {

          .fixed-square-wrapper {
            width:100%;
            position:relative;
            padding: 14em;
            margin: auto 0;
          }
          .fixed-text {
            position:relative;
            transform: translateY(0);

          }

}


  /* EXTERNAL CSS SOURCES
